import React from 'react'
import './IconManage.less'

export function IconMore(props) {
    return (
        <div className={'IconMore ' + props.className}>
            {props.children}
        </div>
    )
}

export function IconChina(props) {
    return (
        <div className={'IconChina ' + props.className}>
            <img src={require("../../Assets/miniIcon/ChinaLogo.png")} alt="ChinaLogo" />
        </div>
    )
}

export function IconButton(props) {
    return (
        <div className='IconButton' onClick={props.onClick}>
            <div className='mainButton' >
                {props.children}
            </div>
        </div>
    )
}

