import React, { useEffect, useImperativeHandle, useState } from 'react'

import './Canvas.less'

export default function Canvas(props) {
    const cRef = React.createRef();
    // const [first, setfirst] = useState(second)
    let context;
    let width;
    let height;
    //缓存数据
    // let imageArr = [];
    const [imageArr, setImageArr] = useState([])

    const Pen = () => {
        let c = cRef.current
        context = cRef.current.getContext('2d')
        context.lineWidth = 4
        context.strokeStyle = 'black'
        c.className = 'pen'
    }

    const Eraser = () => {
        let c = cRef.current
        context = cRef.current.getContext('2d')
        context.lineWidth = 15
        context.strokeStyle = 'white'
        c.className = 'eraser'
    }

    const addTag = () => {
        console.log('addTag')
        let data = toData();
        // (imageArr.length === 20) && (imageArr.shift()); // 上限为储存10步
        if (imageArr.length === 100) {
            imageArr.shift()
        }
        imageArr.push(data)
        setImageArr([...imageArr])
    }

    const undo = () => {
        console.log(imageArr)
        if (imageArr.length <= 2) return false;
        // context.clearRect(0, 0, width, height);
        imageArr.pop()
        setImageArr([...imageArr])
        let context = cRef.current.getContext('2d')
        context.putImageData(imageArr[imageArr.length - 1], 0, 0);
    }

    const clear = () => {
        let context = cRef.current.getContext('2d')
        addTag();
        context.fillStyle = "white";
        context.fillRect(0, 0, width, height);
    }

    // 导出二进制文件用于撤销操作
    const toData = () => {
        return context.getImageData(0, 0, width, height);
    }

    // 导出png文件
    const toImage = () => {
        var image = cRef.current.toDataURL("image/png")
        return image
    }

    const getData = ()=> {
        return imageArr[imageArr.length-1].data
    }

    // Save | Download image
    function downloadImage(filename = 'untitled.jpeg') {
        const data = toImage();
        var a = document.createElement('a');
        a.href = data;
        a.download = 'untitled.jpeg';
        document.body.appendChild(a);
        a.click();
    }

    // 暴露方法
    useImperativeHandle(props.onRef, () => {
        return {
            Pen, Eraser, clear, toData, toImage, downloadImage, undo,getData
        }
    })

    useEffect(() => {
        let c = cRef.current
        context = cRef.current.getContext('2d')
        // setContext(cRef.current.getContext('2d'))
        width = c.width
        height = c.height

        context.lineJoin = 'round'
        // context.lineWidth = 3
        // context.strokeStyle = 'black'
        Pen()

        let drawing = false
        let prevMouseX = 0
        let prevMouseY = 0
        let connectLine = false

        // 背景填充
        clear()
        // 添加初始化
        addTag()
        // 绘画需要
        const getMouseX = (e) => {
            if (typeof e.targetTouches !== 'undefined') {
                return e.targetTouches[0].pageX - c.offsetLeft
            }
            return e.pageX - c.offsetLeft
        }

        const getMouseY = (e) => {
            if (typeof e.targetTouches !== 'undefined') {
                return e.targetTouches[0].pageY - c.offsetTop
            }
            return e.pageY - c.offsetTop
        }

        const draw = (e) => {
            const mouseX = getMouseX(e)*0.64
            const mouseY = getMouseY(e)*0.64

            context.beginPath()
            if (connectLine) {
                context.moveTo(prevMouseX, prevMouseY)
            } else {
                context.moveTo(mouseX, mouseY)
            }
            context.lineTo(mouseX, mouseY)
            context.closePath()
            context.stroke()
            prevMouseX = mouseX
            prevMouseY = mouseY
            connectLine = true
        }

        c.addEventListener('mousedown', (e) => {
            e.preventDefault()
            // context.putTag()
            drawing = true
            draw(e)
        })

        c.addEventListener('mousemove', (e) => {
            if (drawing) {
                e.preventDefault()
                draw(e)
            }
        })

        c.addEventListener('mouseleave', (e) => {
            connectLine = false
        })

        document.addEventListener('mouseup', (e) => {
            if (drawing) {
                addTag()
            }
            drawing = false
            connectLine = false
        })

        c.addEventListener('touchstart', (e) => {
            e.preventDefault()
            // context.putTag()
            drawing = true
            draw(e)
        })

        c.addEventListener('touchmove', (e) => {
            if (drawing) {
                e.preventDefault()
                draw(e)
            }
        })

        document.addEventListener('touchend', (e) => {
            if (drawing) {

            }
            drawing = false
            connectLine = false
        })

        document.addEventListener('touchcancel', (e) => {
            if (drawing) {
                // context.putTag()
            }
            drawing = false
            connectLine = false
        })
    }, [])

    return (
        <div className={'Canvas ' + props.className}>
            <canvas
                width={props.width}
                height={props.height}
                ref={cRef}
                className={'canvas'}
            ></canvas>
            {/* <button onClick={downloadImage}>downloadImage</button>
            <button onClick={clear}>clear</button>
            <button onClick={undo}>undo</button>
            <button onClick={addTag}>addTag</button>
            <button onClick={Pen}>Pen</button>
            <button onClick={Eraser}>Eraser</button> */}
        </div>
    )
}
